@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url('img/BG1.jpg');
  background-size: cover;
  background-repeat: repeat;
  background-position: fill;
  display: flex;
  justify-content: center;
  align-items: center;
}


.App {
  text-align: center;
}

h1 {
  color: #1ae0ee;
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}

.TodoWrapper {
  background: #db2db5;
  margin-top: 5rem;
  padding: 2rem;
  border-radius: 5px;
}

.TodoForm {
  width: 100%;
  margin-bottom: 1rem;
}

.todo-input {
  outline: none;
  background: none;
  border: 1px solid #8758ff;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: #db2db5;
  border-radius: 10px; 
}


.todo-input::placeholder {
  color: #ffffff4d;
}

.todo-btn {
  background: #8758ff;  
  color: #f7fc00;       
  border: none;         
  padding: 0.55rem;     
  cursor: pointer;     
  border-radius: 8px;   
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #8758ff;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 5px;
  margin-bottom: 1rem;
}

.fa-trash {
  margin-left: 0.75rem;
}

.incompleted {
  color: #ece033;
  cursor: pointer;
}

.completed {
  color: #c5aeff;
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
}
 